/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Optional: Add Google Fonts for Inter */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

/* Custom toggle switch styling (basic example, can be replaced with a library) */
/* This styling is for the visual appearance of the dark mode toggle switch */
input[type="checkbox"].toggle {
  /* Hide default checkbox */
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  /* Basic size and shape */
  width: 40px;
  /* Slightly wider */
  height: 24px;
  /* Slightly taller */
  border-radius: 9999px;
  position: relative;
  cursor: pointer;
  /* Background colors */
  background-color: #cbd5e0;
  /* light mode: gray-300 */
  transition: background-color 0.3s ease-in-out;
}

/* Dark mode background for the toggle track */
.dark input[type="checkbox"].toggle {
  background-color: #3f3f46;
  /* zinc-700 for dark mode track */
}

/* Toggle knob */
input[type="checkbox"].toggle::before {
  content: '';
  position: absolute;
  width: 20px;
  /* Slightly larger knob */
  height: 20px;
  /* Slightly larger knob */
  border-radius: 9999px;
  background-color: white;
  top: 2px;
  left: 2px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  /* Subtle shadow for knob */
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

/* Toggle knob when checked (moved to right) */
input[type="checkbox"].toggle:checked::before {
  transform: translateX(16px);
  /* Adjusted for new width/height */
}

/* Toggle knob color when checked (optional, can match accent) */
input[type="checkbox"].toggle:checked {
  background-color: #6366f1;
  /* indigo-500 for checked state */
}

/* Ensure the html element itself gets the dark class */
html.dark {
  /* This is handled by the useEffect in App.js */
}


/* Custom toggle switch styling (basic example, can be replaced with a library) */
.toggle {
  appearance: none;
  width: 38px;
  height: 20px;
  border-radius: 9999px;
  background-color: #cbd5e0;
  /* gray-300 */
  position: relative;
  cursor: pointer;
  transition: background-color 0.3s;
}

.toggle:checked {
  background-color: #4f46e5;
  /* indigo-600 */
}

.toggle::before {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 9999px;
  background-color: white;
  top: 2px;
  left: 2px;
  transition: transform 0.3s;
}

.toggle:checked::before {
  transform: translateX(18px);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}